.zx-visual-editor {
  float: right;
  min-width: 220px; }

.zx-visual-editor-block .visual-editor-actions-block {
  position: relative;
  right: 0;
  display: none; }

.zx-visual-editor-block .visual-editor-content {
  overflow: auto; }

.zx-visual-editor-block .visual-editor-actions {
  position: absolute;
  right: -1px;
  top: -31px;
  background: #2f353a;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  color: #fff;
  border-radius: 3px 3px 0 0;
  font-size: 13px;
  font-weight: normal;
  cursor: pointer;
  height: 32px;
  line-height: 32px; }

.zx-visual-editor-block i {
  padding-right: 10px; }

.zx-visual-editor-block a {
  padding: 0 11px; }

.zx-visual-editor-block a:hover {
  color: #00b9eb !important; }
